<template>
<div class="car">
<form @submit.prevent method="post" action="">
<table>
<tr>
    <th>商品</th>
    <th>名稱</th>
    <th>數量</th>
    <th>樣式</th>
    <th>單價</th>
    <th>備註</th>
    <th></th>
</tr>
<!--一個商品-->
<tr v-for="item in carData" :key="item.id">
    <td class="car1"><img :src="'img/staff/'+item.圖片[0]" class="carimg"/></td>
    <td class="goodsname"><input type="hidden" :id="'goodsname'+item.id" :value=" item.id" />{{ item.name }}</td>
    <td class="number"><input type="number" class="number" :id="'number'+item.id" value="1" min="1" /></td>
    <td class="selectOption">
        <select id="selectOption">
            <option v-for="which in item.樣式" :key="which.id" :value="which" >{{which}}</option>
        </select>
    </td>
    <td class="price1"><input type="hidden" :id="'price'+item.id" :value="item.價格"/>$ {{ item.價格 }}</td>
    <td class="message"><textarea class="textarea" :id="'message'+item.id" ></textarea></td>
    <td class="remove"><button class="removeb" @click="removestaff(item.id)">移除商品</button></td>
</tr>
<!--一個商品end-->
</table>
    <input class="sub" type="submit" value="結帳"/>
</form>
</div>
<!--購物車end-->
<div class="other">
    <a class="card" :href="'#/Staff/401'">
             <img :src="'img/staff/40101.jpg'" class="card-img-top" alt="...">
             <div class="card-body">
                <h5 class="card-title">【ob11】背景立牌 </h5>
                <p class="price">$300</p>
            </div>
    </a>
    <a class="card" :href="'#/Staff/204'">
             <img :src="'img/staff/20401.jpg'" class="card-img-top" alt="...">
             <div class="card-body">
                <h5 class="card-title">【ob11】鯊魚拖鞋 </h5>
                <p class="price">$99</p>
            </div>
    </a>
    <a class="card" :href="'#/Staff/103'">
             <img :src="'img/staff/10301.jpg'" class="card-img-top" alt="...">
             <div class="card-body">
                <h5 class="card-title">【ob11】神職套裝 </h5>
                <p class="price">$399</p>
            </div>
    </a>
    <a class="card" :href="'#/Staff/101'">
             <img :src="'img/staff/10101.jpg'" class="card-img-top" alt="...">
             <div class="card-body">
                <h5 class="card-title">【ob11】狐狸熊貓外套 </h5>
                <p class="price">$299</p>
            </div>
    </a>
</div>

</template>
  
<script>
import { ref,watch} from 'vue';
import axios from 'axios';
import Cookies from 'js-cookie';


export default {
  setup() {
    const jsonData = ref([]);
    const dataLoaded = ref(false);
    const fetchData = async () => {
      try {
        const response = await axios.get('https://arlan95220.github.io/windshop/data/staff.json');
        jsonData.value = response.data.classf;
        dataLoaded.value = true;
      } catch (error) {
        console.error('There was an error!', error);
      }
    };

    const cookieNames =  Cookies.get();
    const car= [];

    Object.entries(cookieNames).forEach(([name, value]) => {
    if (value === 'windy_car') {car.push(name);}
    });

    
    const carData= ref([]);
    const fetchData2 = async () => {
      try {
        const response = await axios.get('https://arlan95220.github.io/windshop/data/staff.json');
        for(let i =0 ; i< 5;i++){
            for(let r =0 ; r<response.data.classf[i].staffs.length;r++){
                for(let t=0 ; t<car.length;t++){
                    if(response.data.classf[i].staffs[r].id === car[t]){
                    carData.value.push(response.data.classf[i].staffs[r]);
                }
                }
            }
        }
      } catch (error) {
        console.log('There was an error!', error);
      }
    };

    watch(dataLoaded);
    fetchData();
    fetchData2();

    const removestaff =(id)=>{
        Cookies.remove(id);
        window.location.reload();
    }

    console.log(carData);
    return { jsonData, dataLoaded,carData,removestaff};
  }
};

</script>
  
<style>
.car{
    margin: 2vw 10vw;
    width: 80vw;
}
.car table{
    width: 80vw;
    font-size: 1.2vw;
}
.car th{
    padding: 0.3vw;
    background-color: #333;
    color:#FFF;
    border: #FFF solid 0.1vw;
}

.car1{
    width: 12vw;
}
.carimg{
    width: 11vw;
    padding: 1vw;
}
.car td{
    background-color: #FFF !important;
    border-right: #333 solid 0.1vw;
    border-bottom: #333 solid 0.1vw;
    border-left: #FFF solid 0.1vw;
    padding: 0.5vw;
}

.remove{
    width: 8vw;
}
.removeb{
    background-color: #333;
    color: #FFF;
}
.goodsname{
    width: 20vw;
    font-size: 1.3vw;
    font-weight: bold;
}
.number{
    width: 5vw;
}

.selectOption{
    width: 8vw;
}
#selectOption{
    width: 8vw;
    margin: 1vw;
}

.price1{
    width: 6vw;
}

.textarea{
    resize: none;
    width: 100%;
    height: 11vw;
}
.car .sub{
    margin: 1vw 70vw;
    background-color: #333;
    border: #CCC solid 0.2vw;
    width: 10vw;
    color: #FFF;
    font-size: 1.3vw;
    font-weight: bold;
    border-radius: 0.4vw;
    padding: 0.2vw;
}
.other{
    width: 100vw;
    margin: 1vw 12vw;
    display: flex;
    flex-wrap: wrap; 
}
.other .card{
    margin-right: 3vw !important;
}
</style>