<template>
    <div class="StaffCon">
        <div class="Classification">
            <button v-for="item in jsonData" :key="item.id" v-bind:onclick="'window.location.href = `#/Home/' + item.id + '`'">
                > &nbsp;{{item.name}} ({{ item.staffs.length }})
            </button>
    </div>

    <div class="staff" >
        <div>
            <div id="carouselExampleControls" class="carousel slide" data-bs-ride="carousel">
                <div class="carousel-inner" style="width: 32vw;">
                    <div class="carousel-item active" >
                        <img :src="'img/staff/'+ sData.圖片[0]"  alt="..." class="mainimg">
                    </div>
                    <div class="carousel-item" v-for="imgs in sData.圖片.slice(1)" :key="imgs">
                        <img :src="'img/staff/'+ imgs "  alt="..." class="mainimg">
                    </div>
                </div>
                <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="prev">
                    <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span class="visually-hidden">Previous</span>
                </button>
                <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="next">
                    <span class="carousel-control-next-icon" aria-hidden="true"></span>
                    <span class="visually-hidden">Next</span>
                </button>
            </div>
        </div>
        <div class="staffDescribe">
                <h5>【ob11】{{ sData.name }}</h5>
                <p ><br> 規格: {{ sData.規格 }}<br>
                    樣式: <span v-for="index in sData.樣式" :key="index">
                        {{ index }} {{ index !== (index.length-1) ? ' ' : '' }}
                    </span><br>
                        價格: <span style="color:red;"> ${{ sData.價格 }}</span>
                        <br><br>
                        ⚠️嚴禁跑單⚠️<br>
                        1.”超商取貨”不取貨者，之後一律禁止下標<br>
                        2.商品有個體差異或些許瑕疵，完美主義勿下單<br>
                        3.本店會盡力避免碰撞，但無法完全避免，介意者勿下單<br>
                        4. 照片光線跟濾鏡多少有些色差，介意者勿下單<br>
                        5. 開箱請錄影，確保自身權益，未錄影無法退貨，漏寄或少物件也需錄影作證<br>
                </p>

                <button class="add" @click="addstaff">加入購物車</button>
        </div>
    </div>
</div>
</template>
  
<script>
import { ref,watch } from 'vue';
import axios from 'axios';
import { useRoute } from 'vue-router';
import Cookies from 'js-cookie';

export default {
  mounted() { },
  setup() {
    const jsonData = ref([]);
    const $route = useRoute();
    const dataLoaded = ref(false);
    const dataLoaded2 = ref(false);
    const fetchData = async () => {
      try {
        const response = await axios.get('https://arlan95220.github.io/windshop/data/staff.json');
        jsonData.value = response.data.classf;
        dataLoaded.value = true;
      } catch (error) {
        console.error('There was an error!', error);
      }
    };

    const sid = $route.params.id;
    const scl = sid.substring(0, 1);
    const sData = ref([]);
    const fetchData2 = async () => {
      try {
        const response2 = await axios.get('https://arlan95220.github.io/windshop/data/staff.json');
        sData.value = response2.data.classf[scl].staffs;
        for(let i = 0; i < sData.value.length; i++){
            if(sData.value[i].id == $route.params.id){
                sData.value = sData.value[i];
            }
        }
        dataLoaded2.value = true; 
      } catch (error) {
        console.error('There was an error!', error);
      }
    };

    const addstaff = () => {
      Cookies.set($route.params.id , 'windy_car', { expires: 30 });
      alert('成功加入購物車');
    };

    watch(dataLoaded,dataLoaded2);

    // 在组件挂载完成后加载数据
    fetchData();
    fetchData2();
    return { jsonData, dataLoaded,sData,addstaff};
  },
};

</script>
  
<style>
.StaffCon{
    min-height: 70vh;
    display: flex;
    padding: 1vw;
}
.staff{
    margin: 1vw 2vw;
    width: 78vw;
    height: 36vw;
    flex-wrap: nowrap;
    background-color: #EEE;
    padding: 1vw;
    display: flex; 
}

.mainimg{
    width: 32vw;
    height: 32vw;
    margin: 1vw;
}
.staff .staffDescribe{
    margin: 1.5vw;
}
.staff h5{
    font-size: 2.3vw;
    font-weight: bold;
    height: 3vw;
}

.staff p{
    font-size: 1.2vw;
    font-weight: bold;
    color:#666;
}
.staff .add{
    background-color: #555;
    border: #CCC solid 0.2vw;
    color: #FFF;
    font-size: 1.3vw;
    font-weight: bold;
    border-radius: 0.4vw;
    padding: 0.5vw 1.5vw;
    float: right;
    height: 3vw;
}
</style>