<template>
<div class="staffpage">
    
    <div class="Classification">
            <button v-for="item in jsonData" :key="item.id" v-bind:onclick="'window.location.href = `#/Home/' + item.id + '`'">
                > &nbsp;{{item.name}} ({{ item.staffs.length }})
            </button>
    </div>
    <div class="staffs" v-if="$route.params.id === '2'" >
        <div v-for="staff in jsonData[1].staffs" :key="staff.id">
        <a class="card" :href="'#/Staff/'+staff.id" >
             <img :src="'img/staff/'+ staff.圖片[0]" class="card-img-top" alt="...">
             <div class="card-body">
                <h5 class="card-title">【ob11】{{staff.name}} </h5>
                <p class="price">${{staff.價格}}</p>
            </div>
        </a>
        </div>
    </div>

    <div class="staffs" v-else-if="$route.params.id === '3'">
        <div v-for="staff in jsonData[2].staffs" :key="staff.id">
            <a class="card" :href="'#/Staff/'+staff.id" >
             <img :src="'img/staff/'+ staff.圖片[0]" class="card-img-top" alt="...">
             <div class="card-body">
                <h5 class="card-title">【ob11】{{staff.name}} </h5>
                <p class="price">${{staff.價格}}</p>
            </div>
        </a>
        </div>
    </div>

    <div class="staffs" v-else-if="$route.params.id === '4'">
        <div v-for="staff in jsonData[3].staffs" :key="staff.id">
            <a class="card" :href="'#/Staff/'+staff.id" >
             <img :src="'img/staff/'+ staff.圖片[0]" class="card-img-top" alt="...">
             <div class="card-body">
                <h5 class="card-title">【ob11】{{staff.name}} </h5>
                <p class="price">${{staff.價格}}</p>
            </div>
        </a>
        </div>
    </div>

    <div class="staffs" v-else-if="$route.params.id === '5'">
        <div v-for="staff in jsonData[4].staffs" :key="staff.id">
            <a class="card" :href="'#/Staff/'+staff.id" >
             <img :src="'img/staff/'+ staff.圖片[0]" class="card-img-top" alt="...">
             <div class="card-body">
                <h5 class="card-title">【ob11】{{staff.name}} </h5>
                <p class="price">${{staff.價格}}</p>
            </div>
        </a>
        </div>
    </div>

    <div class="staffs" v-else-if="$route.params.id === '1'">
        <div v-for="staff in jsonData[0].staffs" :key="staff.id">
            <a class="card" :href="'#/Staff/'+staff.id" >
             <img :src="'img/staff/'+ staff.圖片[0]" class="card-img-top" alt="...">
             <div class="card-body">
                <h5 class="card-title">【ob11】{{staff.name}} </h5>
                <p class="price">${{staff.價格}}</p>
            </div>
        </a>
        </div>
    </div>

    <div class="staffs" v-else>
        <div v-for="staff in jsonData[0].staffs" :key="staff.id"></div>
        <a class="card" :href="'#/Staff/'+staff.id" >
             <img :src="'img/staff/'+ staff.圖片[0]" class="card-img-top" alt="...">
             <div class="card-body">
                <h5 class="card-title">【ob11】{{staff.name}} </h5>
                <p class="price">${{staff.價格}}</p>
            </div>
        </a>
    </div>
    

</div>
</template>
  
<script>
import { ref,watch } from 'vue';
import axios from 'axios';


export default {
  setup() {
    const jsonData = ref([]);
    const dataLoaded = ref(false);
    const fetchData = async () => {
      try {
        const response = await axios.get('https://arlan95220.github.io/windshop/data/staff.json');
        jsonData.value = response.data.classf;
        dataLoaded.value = true; // 设置数据加载完成的标志
      } catch (error) {
        console.error('There was an error!', error);
      }
    };

        // 使用 watch 监听数据加载状态
    watch(dataLoaded, (newValue) => {
      if (newValue) {
        // 数据加载完成后执行渲染页面的操作
        console.log('Data loaded successfully');
      }
    });

    // 在组件挂载完成后加载数据
    fetchData();

    return { jsonData, dataLoaded };
  }

};
</script>
  
<style>
.staffpage{
    min-height: 70vh;
    display: flex;
    padding: 1vw;
}
.Classification{
    width: 20vw;
    margin: 1vw;
    display: flex;
    flex-direction: column;
}
.Classification button{
    padding: 0.5vw 1vw;
    border: #777 solid 0.1vw;
    text-align: left;
    font-size: 1.2vw;
    font-weight: bold;
}
.staffs{
    margin: 0vw 2vw;
    width: 78vw;
    display: flex;
    flex-wrap: wrap; 
}
.card{
    width: 16vw;
    height: 24vw;
    overflow:hidden;
    text-decoration: none;
    margin: 1vw 1vw;
}

.card-img-top{
    width: 16vw;
    height: 16vw !important;
    border-radius: 0px;
}
.card-body{
    padding: 1vw 1vw;
}
.card-title{
    font-size: 1.2vw;
    font-weight: bold;
}
.price{
    color: red;
    font-size: 1.2vw;
    position: absolute;
    bottom: -1vw;
    right: 1vw;
}
</style>