import Home from '../HomeI.vue';
import Staff from '../StaffPage.vue';
import About from '../AboutUs.vue';
import ShoppingCar from '../ShoppingCar.vue';
import { createWebHashHistory, createRouter } from "vue-router";

	const routes =[		{
		path: "/",
		name: "Home",
		component: Home,
	},
		{
			path: "/Home/:id",
			name: "Home",
			component: Home,
		},
		{
			path: "/Staff/:id",
			name: "Staff",
			component: Staff,
		},{
			path: "/About",
			name: "About",
			component: About,
		},{
			path: "/ShoppingCar",
			name: "ShoppingCar",
			component: ShoppingCar,
		},
	];

	const router = createRouter({ history: createWebHashHistory(),base: './windshop/',routes })
	export default router;